.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-main {
  background-image: url("/public/images/bg-main-desktop.png");
  background-repeat: no-repeat;
  background-color: rgb(255, 255, 255);
  min-height: 100vh;
  background-size: 100vh;
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cards{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 500px;
  color: white;
  margin-right: 50px;
  height: 360px;
}

.card1{
  width: 100%;
}

.card2{
  width: 100%;
  display: flex;
  justify-content:center;
  flex-direction: column;
  align-items:flex-end;
}

.front-card{
  display: flex;
  justify-content:flex-start;
  align-items: center;
  flex-direction: column;
  background-image: url("/public/images/bg-card-front.png");
  background-size: 300px;
  background-repeat: no-repeat;
  width: 300px;
  height: 165px;
  background-position: center;
}

.esferas{
  height: 40%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
}
.bola1{
  margin: 20px;
  margin-right: 10px;
  height: 30px;
  width: 30px;
  border-radius: 100%;
  background-color: rgb(255, 255, 255);
}

.bola2{
  height: 10px;
  width: 10px;
  border-radius: 100%;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
}


.back-card{
  display: flex;
  justify-content:flex-end;
  align-items: center;
  background-image: url("/public/images/bg-card-back.png");
  background-size: 300px;
  background-repeat: no-repeat;
  width: 300px;
  height: 165px;
  background-position: center;
  margin-top: 20px;
}

.formulario-card{
  width: 400px;
  display: flex;
  justify-content: center;
  align-items: self-start;
  flex-direction: column;
}

.date-cvc, .name-date{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}



.date{
  display: flex;
  align-items:flex-start;
  flex-direction: column;
  justify-content:center;
}

.cvc, #cvc{
  text-align: left;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 225px;
}

.dados{
  cursor: pointer;
  padding: 8px;
  border-radius: 5px;
  border-color: rgba(0, 0, 0, 0.123);
  color: rgba(0, 0, 0, 0.67);
  border-width: 2px;
  font-size: 15px;
  width: 95%;
  font-weight: bold
} .dados::-webkit-inner-spin-button,
  .dados::-webkit-outer-spin-button {
    -webkit-appearance: none;
} .dados::placeholder{
  color: rgba(0, 0, 0, 0.363);
  font-weight: bold
}


.mm-yy{
  width: 155px;
  display: flex;
  flex-direction: row;
  align-items:center;
}

#mm, #yy{
  width: 40px;
  margin-right:20px;

}

.titulo, .dados-card{
  color: black;
  font-family:monospace;
  text-transform: uppercase;
  font-size: 13px;
  margin-top: 20px;
  font-weight: bold;
}

.number-card, .dados-card, .thank-you{
  margin: 0;
  font-family:monospace;
  margin-top: 20px;
  color: white;
}

#mm-card{
  margin-left: 100px;
}
#cvc-card{
  margin: 0;
  margin-right: 40px;
}

.botao{
  width: 400px;
  font-size: 15px;
  padding:9px;
  margin-top:20px;
  border-radius: 5px;
  border: 0px;
  color: rgb(255, 255, 255);
  background-color: #1d0339;
  cursor: pointer;
  font-family: monospace;
}

.error-blank{
  margin: 0;
  padding: auto;
  color: red;
  font-weight:400;
  font-size: 12px;
  display: none;
}

.card-add{
  width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.thank-you{
  font-family:monospace;
  margin-top: 10px;
  color: #000000;
  text-transform: uppercase;
  font-size: 30px;

}

#descricao-card-add{
  color: rgba(0, 0, 0, 0.67);
  font-weight: bold;
  text-transform: none;
  font-size: 14px;
}

.icon-add{
  height: 65px;
}



/* //////////////////////////////     MOBILE        */



@media screen and (max-width: 1024px) {
  
  .App-main{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    min-height: 0;
  }

  .App-main{
    background-image: url("/public/images/bg-card-back.png") top;
    background-size: 100% 300px
  }

  .cards{
    margin: 80px 0px 0px 0px;
    width: 360px;
    height: 280px;
    flex-direction: column-reverse;
    justify-content:flex-end;
  }

  .back-card{
    margin-top: 0;
  }

  .front-card{
    position: absolute;
    top: 180px;
  }
  
  .dados-card{
    font-size: 14px;
  }

  .titulo{
    font-size: 16px;
  }

  .formulario-card{
    width: 350px;
  }

  .dados{
    height: 20px;
    font-size: 18px;
  }

  .botao{
    width: 350px;
    height: 40px;
    font-size: 18px;
  }

  .mm-yy{
    width: 155px;
    display: flex;
    flex-direction: row;
    align-items:center;
  }

  #mm, #yy{
    width: 40px;
    margin-right:15px;
  
  }

  .cvc, #cvc{
    width: 175px;
  }
}